
.MainBox_home{
    height: 430px;
}
.container_1{
    display: flex;
    align-items: center;
    justify-content: space-around;
    height: 500px;
    width: 100%;
}
.image_logo{
    height: 350px;
    width: 350px;
    background-image: url(./Book.png);
    background-repeat: no-repeat;
    background-position: center;
}
.Main_image{
    height: 300px;
    width: 680px;
    background-image: url(./Name_title.png);
    background-repeat: no-repeat;
    background-position: center;
}
.textutilData{
    height: 200px;
    width: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
}
@media only screen and (max-width: 380px) {
    .container_1 {
        flex-direction: column;
        width: 100%;
    }
    .image_logo{
        height: 350px;
        width: 350px;
        margin-top: 12px;
    }
    .Main_image{
        display: none;
    }
    .textutilData{
        width: 100%;
        text-align: center;
    }
}

